import React, { useState } from 'react'
import ProjectData from '../types/Project.type'
import ProjectCard from '../components/Project.component'
import Animation from '../components/Animation.component'

import { getProjects, searchProjects } from '../services/api'

const ProjectList: React.FC<any> = () => {
  const projects: Array<any> = getProjects()
  const [ projectList, setProjectList ] = useState( projects )

  const [ searchForState, setSearchForState ] = useState( '' )
  const search = (e: { target: { value: string } } ) => {
    const searchFor = e.target.value.toLowerCase()
    if( searchFor.length >= 2 || searchFor == '' ) {
      const searchItems = searchProjects( searchFor )
      setProjectList( searchItems )
    }
    setSearchForState( searchFor )
  }

  return (
    <>
      <section className='project-list-hero'>
        <div className='section-inner'>
          <div className='section-middle'>
            <Animation>
              <h1>
                Work
              </h1>
            </Animation>
          </div>
        </div>
      </section>
      <section className='project-list-listings'>
        <div className='section-inner'>
          <div
            className='search-wrapper'
            >
            <input
              type='search'
              onChange={ search }
              placeholder='search'
              className='search-projects'
              />
          </div>
          <ul className='project-list'>
            { projectList && projectList.map( ( project: ProjectData, index: number ) => ( 
              <ProjectCard 
                {...project} 
                key={ index } 
                />
            ) ) }
          </ul>
        </div>
      </section>
    </>
  )
}

export default ProjectList
