import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import ProjectData from '../types/Project.type'
import MediaData from '../types/Media.type'
import MediaCard from './Media.component'

const renderImages = ( media: MediaData[] ) => {
  return media.map( ( media: MediaData, index: number ) => {
    if( index === 0 ) return <MediaCard {...media} key={ index } />
      else return ''
  } )
}

const ProjectCard: React.FC<ProjectData> = ( {
  id,
  title,
  client,
  completeddate,
  background,
  theme,
  keywords,
  hover,
  media
} ) => {
  const fullUrl = '/work/' + title.toLowerCase().replace(/ /g, '_')
  const [ toShow, setToShow ] = useState(false)
  const handleMouseEnter = ( e: any ) => {
    const target = e.target
    const url = target.dataset.background
    setToShow( true )
  }
  const handleMouseLeave = ( e: any ) => {
    setToShow( false )
  }

  const Keywords = () => {
    return keywords.map( ( keyword: string, index: number ) => {
      return <li key={ index }>keyword</li>
    } )
  }

  return (
    <li 
      key={ id } 
      className='project-card' 
      onMouseEnter={ handleMouseEnter }
      onMouseLeave={ handleMouseLeave }
      >
      <div
        className={ toShow ? 'zoom-in' : 'zoom-out' }
        >
        <div
          className='background-image'
          style={ { 
            backgroundColor: theme && theme.background ? theme.background : 'white',
            backgroundImage: `url(${ hover })` ,
          } }
          >
        </div>
      </div>
      <Link to={ fullUrl } >
        <p> { client }</p>
        <h2 
          style={{ color: background ? background : theme && theme.primary ? theme.primary : 'white' }}
          >
            { title }
        </h2>
        <ul className='pills'>
          {
            keywords ? (
              keywords.map( ( keyword: string, index: number ) => {
                return <li key={ index }>{ keyword }</li>
              } )
            ) : (
              ''
            ) 
          }
        </ul>
      </Link>
    </li>
  )
}

export default ProjectCard
