import React, { useRef } from 'react'
import MediaData from '../types/Media.type'
import Animation from './Animation.component'

const MediaCard: React.FC<MediaData> = ( {
  url,
  alt,
  mediatype,
  description,
} ) => {

  return (
    <Animation>
      <div 
        className='preview'
        style={ { 
          backgroundImage: `url(${ url })` 
        } }
        >
      </div>
      { description 
        ? <p className='image-description'>{ description }</p> 
        : <p className='image-description'>&nbsp;</p>
      }
      
    </Animation>
  )
}

export default MediaCard
